import React from 'react'
import '../css/style.css'
import { $ } from 'jquery'


$(document).ready(function(){
    $("#active").css("");
})
function Home() {
    return (
        <div className="home">
            <nav>
                <div className="first__layer__nav">
                    <div className="first__layer__nav__flex">
                        <div className="first__layer__nav__flex__1">
                            <div id="active" className="verticalAlign text-wrap h1-hover">
                                <h1>Privatkunde</h1>
                            </div>
                        </div>
                        <div className="first__layer__nav__flex__2">
                            <div className="verticalAlign h1-hover text-wrap">
                                <h1>Firmenkunde</h1>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Home
